import type { isHideSelectionQuery } from '@ali/4ever-cangjie';

const isHideSelection: isHideSelectionQuery = (
  controller,
) => {
  const { selectedCallout = null } = controller.value.data;
  // 按仓颉中写法 ret === undefined 才能走到 next, 即表格的 isHideSelection
  return selectedCallout ? true : undefined;
}

export default isHideSelection;
